import ajax from '../../utils/ajax.js'
export default {
	data() {
		return {
			goodDetail:{}
		}
	},
	async mounted(){
		/*
			如何获取路由传参的参数
			this.$mp
		*/
	   let { goodId }= this.$mp.query;
	   let goodDetail  = await ajax('/getgooddetail',{
		   goodId
	   })
	   this.goodDetail = goodDetail;
	   // console.log('result',result)
	},
	methods:{
		addShopItem(){
			this.$store.commit('addShopItem',this.goodDetail)
		}
	}
}
